<template>
  <div class="admin-users">
    <h1>إدارة المستخدمين</h1>
    
    <!-- حالة التحميل -->
    <div v-if="loading" class="loading">جاري تحميل المستخدمين...</div>
    
    <!-- جدول المستخدمين -->
    <div v-else class="users-table-container">
      <table class="users-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>البريد الإلكتروني</th>
            <th>تاريخ الإنشاء</th>
            <th>عدد الرسائل</th>
            <th>حالة الاشتراك</th>
            <th>نهاية الاشتراك</th>
            <th>الإجراءات</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" :key="user.id">
            <td>{{ user.id }}</td>
            <td>{{ user.email }}</td>
            <td>{{ formatDate(user.createdAt) }}</td>
            <td>{{ user.Device ? user.Device.messagesCount : 0 }}</td>
            <td>
              <span :class="getSubscriptionStatusClass(user.Subscription)">
                {{ getSubscriptionStatus(user.Subscription) }}
              </span>
            </td>
            <td>{{ getSubscriptionEndDate(user.Subscription) }}</td>
            <td class="actions">
              <button 
                v-if="user.Device"
                @click="deleteDevice(user.id)"
                class="btn btn-danger"
                :disabled="processingAction"
              >
                حذف الجهاز
              </button>
              <button 
                @click="showExtendModal(user)"
                class="btn btn-primary"
                :disabled="processingAction"
              >
                تجديد الاشتراك
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      
      <div class="pagination">
        <button 
          @click="changePage(currentPage - 1)" 
          :disabled="currentPage === 1"
          class="btn btn-secondary"
        >
          السابق
        </button>
        <span class="page-info">
          الصفحة {{ currentPage }} من {{ totalPages }}
        </span>
        <button 
          @click="changePage(currentPage + 1)" 
          :disabled="currentPage === totalPages"
          class="btn btn-secondary"
        >
          التالي
        </button>
      </div>
    </div>

    <!-- نافذة تجديد الاشتراك -->
    <div v-if="showModal" class="modal-overlay">
      <div class="modal">
        <h3>تجديد الاشتراك للمستخدم {{ selectedUser?.email }}</h3>
        <div class="modal-body">
          <label for="months">عدد الأشهر:</label>
          <input 
            type="number" 
            id="months"
            v-model.number="extendMonths" 
            min="1" 
            max="12"
            class="form-input"
          >
        </div>
        <div class="modal-actions">
          <button @click="extendSubscription" class="btn btn-primary">
            تجديد
          </button>
          <button @click="closeModal" class="btn btn-secondary">
            إلغاء
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'AdminUsers',
  data() {
    return {
      users: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      totalPages: 0,
      totalUsers: 0,
      showModal: false,
      selectedUser: null,
      extendMonths: 1,
      processingAction: false
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    async load() {
      this.loading = true;
      try {
        const response = await axios.get(`/api/admin/users?page=${this.currentPage}&limit=${this.pageSize}`);
        const data = response.data;
        
        if (data.success) {
          this.users = data.users;
          this.totalPages = data.totalPages;
          this.totalUsers = data.totalUsers;
        } else {
          console.error('فشل في تحميل المستخدمين:', data.message);
        }
      } catch (err) {
        alert(err.response?.data?.message || err.message);
      } finally {
        this.loading = false;
      }
    },
    
    async deleteDevice(userId) {
      if (!confirm('هل أنت متأكد من حذف الجهاز؟')) return;
      
      this.processingAction = true;
      try {
        await axios.delete(`/api/admin/users/${userId}/device`);
        alert('تم حذف الجهاز بنجاح');
        this.load(); // تحديث القائمة
      } catch (err) {
        alert(err.response?.data?.message || err.message);
      } finally {
        this.processingAction = false;
      }
    },
    
    showExtendModal(user) {
      this.selectedUser = user;
      this.extendMonths = 1;
      this.showModal = true;
    },
    
    closeModal() {
      this.showModal = false;
      this.selectedUser = null;
    },
    
    async extendSubscription() {
      if (!this.selectedUser || this.extendMonths < 1) return;
      
      this.processingAction = true;
      try {
        await axios.post(`/api/admin/users/${this.selectedUser.id}/subscription/extend`, {
          months: this.extendMonths
        });
        alert('تم تجديد الاشتراك بنجاح');
        this.closeModal();
        this.load(); // تحديث القائمة
      } catch (err) {
        alert(err.response?.data?.message || err.message);
      } finally {
        this.processingAction = false;
      }
    },
    
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
        this.load();
      }
    },
    
    formatDate(dateString) {
      return new Date(dateString).toLocaleDateString('ar-EG');
    },
    
    getSubscriptionStatus(subscription) {
      if (!subscription || subscription.length === 0) return 'لا يوجد اشتراك';
      const activeSub = subscription.find(sub => sub.status === 'active');
      return activeSub ? 'نشط' : 'غير نشط';
    },
    
    getSubscriptionStatusClass(subscription) {
      const status = this.getSubscriptionStatus(subscription);
      return status === 'نشط' ? 'status-active' : 'status-inactive';
    },
    
    getSubscriptionEndDate(subscription) {
      if (!subscription || subscription.length === 0) return 'غير متاح';
      const activeSub = subscription.find(sub => sub.status === 'active');
      return activeSub ? this.formatDate(activeSub.endAt) : 'غير متاح';
    }
  }
}
</script>

